import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { ContactItem } from '../../../../models/types';

@Component({
  selector: 'bb-beneficiary-contact',
  templateUrl: 'beneficiary-contact.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BeneficiaryContactComponent {
  /**
   * Contact item
   */
  @Input()
  set contact(item: ContactItem & { accountName?: string }) {
    this._contact = item;
    if (item?.accounts[0]) {
      this.accountNumber =
        item.accounts[0].accountNumber || item.accounts[0].IBAN || '';
    }
  }
  get contact() {
    return this._contact;
  }
  private _contact!: ContactItem & { accountName?: string };

  accountNumber?: string;
}
